import React from 'react';
import InputForm from './InputForm';
import { Button } from '@scuf/common';

const { Client, Token, ChannelClaim, LocalMedia, AudioStream, VideoStream, RemoteMedia, DomLayoutManager } = require('fm.liveswitch');

const fmServerUrl = "https://demo.liveswitch.fm:8443/sync";
const fmAppId = "my-app-id";
const fmSharedSecret = "--replaceThisWithYourOwnSharedSecret--";


export default class App extends React.Component {

    client;
    layoutRef;
    audioLM;
    videoLM;
    screenLM;
    channel;
    layoutManager;
    upAudioConnection;
    upVideoConnection;
    upScreenConnection;

    state = {
        isAudio: true,
        isVideo: false,
        isScreen: false,
        peers: {},
        remoteMedias: []
    }

    componentDidMount() {
        this.audioLM = new LocalMedia(true, false);
        this.audioLM.start();
        this.videoLM = new LocalMedia(false, true);
        this.screenLM = new LocalMedia(false, true, true);
        this.layoutManager = new DomLayoutManager(this.layoutRef);
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevState.isAudio !== this.state.isAudio) {
            if (this.audioLM) this.audioLM.getAudioTrack().setMuted(!this.state.isAudio);
            this.sendActionStatusToAll();
        }
        if (prevState.isVideo !== this.state.isVideo) {
            if (this.state.isVideo) this.videoLM.start();
            else this.videoLM.stop();
            this.sendActionStatusToAll();
        }
        if (prevState.isScreen !== this.state.isScreen) {
            if (this.state.isScreen) {
                this.screenLM.start().then(() => {
                    this.sendUpScreenConnection();
                });
            } else {
                this.screenLM.stop().then(() => {
                    this.closeUpScreenConnection();
                });
            }
            this.sendActionStatusToAll();
        }
        if (prevState.peers !== this.state.peers || prevProps.remoteMedias !== this.state.remoteMedias) {
            Object.keys(this.state.peers).forEach(userId => {
                if (prevState.peers[userId] || this.state.peers[userId]) {
                    if ((prevState.peers[userId] || {}).isVideo !== (this.state.peers[userId] || {}).isVideo) {
                        const rm = this.state.remoteMedias.find(obj => obj.userId === userId);
                        if (rm) {
                            if (this.state.peers[userId].isVideo) {
                                this.layoutManager.addRemoteView(rm.remoteMedia.getId(), rm.remoteMedia.getView());
                            } else {
                                this.layoutManager.removeRemoteView(rm.remoteMedia.getId());
                            }
                        }
                    }
                }
            });
        }
    }

    toggleAudio = () => {
        this.setState(prevState => ({ isAudio: !prevState.isAudio }));
    }
    
    toggleVideo = () => {
        this.setState(prevState => ({ isVideo: !prevState.isVideo }));
    }

    toggleScreen = () => {
        this.setState(prevState => ({ isScreen: !prevState.isScreen }));
    }

    getActionsStatusAsString = () => {
        return JSON.stringify({ 
            isAudio: this.state.isAudio, 
            isVideo: this.state.isVideo, 
            isScreen: this.state.isScreen 
        })
    }

    sendActionStatusToAll = () => {
        if (this.channel) this.channel.sendMessage(this.getActionsStatusAsString());
    }

    sendAudioVideoUpConnections = () => {
        const audioStream = new AudioStream(this.audioLM, null);
        this.upAudioConnection = this.channel.createSfuUpstreamConnection(audioStream);
        this.upAudioConnection.setTag('AUDIO');
        this.upAudioConnection.open();
    
        const videoStream = new VideoStream(this.videoLM, null);
        this.upVideoConnection = this.channel.createSfuUpstreamConnection(videoStream);
        this.upVideoConnection.setTag('VIDEO');
        this.upVideoConnection.open();
    }

    sendUpScreenConnection = () => {
        const videoStream = new VideoStream(this.screenLM, null);
        this.upScreenConnection = this.channel.createSfuUpstreamConnection(videoStream);
        this.upScreenConnection.setTag('SCREEN');
        this.upScreenConnection.open();
    }

    closeUpScreenConnection = () => {
        this.upScreenConnection.close();
    }

    listenToChannel = () => {
        this.channel.addOnRemoteUpstreamConnectionOpen(remoteConnectionInfo => {
            const type = remoteConnectionInfo.getTag();
            if (type === 'SCREEN' && this.state.isScreen) {
                this.toggleScreen();
            }
            const remoteMedia = new RemoteMedia();
            const userId = remoteConnectionInfo.getUserId();
            let stream;
            if (type === 'AUDIO') {
                stream = new AudioStream(null, remoteMedia);
            } else {
                stream = new VideoStream(null, remoteMedia);
                this.setState(prevState => ({
                    remoteMedias: [...prevState.remoteMedias, { userId, type, remoteMedia }]
                }));
            }
            if (type === 'SCREEN') this.layoutManager.addRemoteView(remoteMedia.getId(), remoteMedia.getView());
            const downConnection = this.channel.createSfuDownstreamConnection(remoteConnectionInfo, stream);
            downConnection.open();
        });
        this.channel.addOnRemoteUpstreamConnectionClose(remoteConnectionInfo => {
            const type = remoteConnectionInfo.getTag();
            const userId = remoteConnectionInfo.getUserId();
            const remoteMediaItem = this.state.remoteMedias.find(rm => rm.userId === userId && rm.type === type);
            if (remoteMediaItem) {
                this.layoutManager.removeRemoteView(remoteMediaItem.remoteMedia.getId());
                this.setState(prevState => ({
                    remoteMedias: prevState.remoteMedias.filter(rm => rm !== remoteMediaItem)
                }));
            }
        });
        this.channel.addOnUserMessage((cl, message) => {
            const userId = cl.getUserId();
            const messageParsed = JSON.parse(message);
            this.setState(prevState => ({
                peers: {...prevState.peers, [userId]: { isVideo: messageParsed.isVideo, isAudio: messageParsed.isAudio, isScreen: messageParsed.isScreen } }
            }));

            if (messageParsed.type !== 'ACKNOWLEDGEMENT') {
                const messageBody = JSON.parse(this.getActionsStatusAsString());
                messageBody.type = 'ACKNOWLEDGEMENT';
                this.channel.sendUserMessage(userId, JSON.stringify(messageBody));
            }

        });
        this.channel.addOnMessage((cl, message) => {
            const userId = cl.getUserId();
            if (userId !== this.channel.getUserId()) {
                const messageBody = JSON.parse(message);
                this.setState(prevState => ({
                    peers: {...prevState.peers, [userId]: { isVideo: messageBody.isVideo, isAudio: messageBody.isAudio, isScreen: messageBody.isScreen } }
                }));
            }
        });
        this.channel.addOnRemoteClientJoin(cl => {
            const userId = cl.getUserId();
            this.channel.sendUserMessage(userId, this.getActionsStatusAsString() );
        });
    }

    handleJoin = info => {
        const { channelId, userId, deviceId } = info;
        this.client = new Client(fmServerUrl, fmAppId, userId, deviceId);
        const token = Token.generateClientRegisterToken(this.client, [new ChannelClaim(channelId)], fmSharedSecret);
        this.client.register(token).then(channels => {
            this.channel = channels[0];
            this.sendAudioVideoUpConnections();
            this.listenToChannel();
        });
    }

    render() {
        return (
            <div>
                <InputForm onJoin={this.handleJoin} />
                <Button onClick={this.toggleVideo} content={`TURN VIDEO ${this.state.isVideo ? 'OFF': 'ON'}`} /> <br />
                <Button onClick={this.toggleAudio} content={`TURN AUDIO ${this.state.isAudio ? 'OFF': 'ON'}`} /> <br />
                <Button onClick={this.toggleScreen} content={`TURN SCREEN ${this.state.isScreen ? 'OFF': 'ON'}`} /> <br />
                <div>{JSON.stringify(this.state.peers)}</div>
                <div className="layout" ref={node => this.layoutRef = node} />
            </div>
        );
    }
}