import React from 'react';
import { Input, Button } from '@scuf/common';

export default props => {
    const { onJoin } = props;
    const [ channelId, setChannelId ] = React.useState('');
    const [ userId, setUserId ] = React.useState('');
    const [ deviceId, setDeviceId ] = React.useState('');
    return (
        <div>
            <div>
                Channel Id: 
                <Input type="text" value={channelId} onChange={setChannelId} />
            </div>
            <div>
                User Id: 
                <Input type="text" value={userId} onChange={setUserId} />
            </div>
            <div>
                Device Id: 
                <Input type="text" value={deviceId} onChange={setDeviceId} />
            </div>
            <div>
                <Button content="JOIN" onClick={() => onJoin({channelId, userId, deviceId}) } />
            </div>
        </div>
    )
}